  outline: 0;
}
a {
  color: inherit;
  text-decoration: none;
}
:root {
  --header-margin-bottom: 5rem;
}
.link {
  margin-bottom: 0.5em;
  transition: all 0.4s ease;
}
.link a {
  color: #006dd5;
  cursor: pointer;
  font-size: 1.6rem;
  margin-right: 0.4rem;
  position: relative;
  text-decoration: none;
  transition: all 0.4s ease;
}
.link a:after {
  background-color: rgba(0,109,213,0.3);
  bottom: 0;
  content: '';
  height: 1px;
  right: 0;
  position: absolute;
  transition: width 0.4s cubic-bezier(0.4, 0, 0, 1);
  width: 0;
}
.link a:hover:after {
  left: 0;
  width: 100%;
}
.link .svg {
  transform: translateY(1px);
}
.link:hover {
  color: #006dd5;
}
.link_active {
  color: #006dd5;
}
