  align-items: center;
  color: #fff;
  cursor: pointer;
  display: inline-flex;
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
  font-size: 1.6rem;
  justify-content: center;
  line-height: 1;
  max-height: 6.4rem;
  overflow: hidden;
  padding: 2.4rem 4rem;
