.form-control {
  margin-bottom: 2rem;
  position: relative;
  width: 100%;
}
.form-control label,
.form-control__label {
  color: #fff;
  cursor: text;
  display: inline-block;
  line-height: 1;
  font-weight: 400;
  margin-bottom: 0.75em;
  transition: all 0.2s;
  max-width: 100%;
}
.form-control input[type="text"],
.form-control input[type="password"],
.form-control input[type="date"],
.form-control input[type="email"],
.form-control input[type="tel"],
.form-control input[type="number"],
.form-control input[type="search"],
.form-control select,
.form-control textarea,
.form-control .custom-select {
  background: #fff;
  border: none;
  border: 1px solid #e3e3e3;
  border-radius: 4px;
  color: #fff;
  display: block;
  font-family: 'Inter', 'Arial', 'Helvetica', sans-serif;
  font-size: 1.6rem;
  line-height: 1;
  min-height: 6.4rem;
  transition: border-color 0.2s;
  padding: 2rem 2.4rem;
  position: relative;
  transition: all 0.4s ease;
  resize: vertical;
  width: 100%;
  -webkit-appearance: none;
}
.form-control input[type="text"] .current,
.form-control input[type="password"] .current,
.form-control input[type="date"] .current,
.form-control input[type="email"] .current,
.form-control input[type="tel"] .current,
.form-control input[type="number"] .current,
.form-control input[type="search"] .current,
.form-control select .current,
.form-control textarea .current,
.form-control .custom-select .current {
  display: inline-block;
  overflow: hidden;
  max-width: 90%;
  vertical-align: middle;
  text-overflow: ellipsis;
}
.form-control input[type="text"]:hover,
.form-control input[type="password"]:hover,
.form-control input[type="date"]:hover,
.form-control input[type="email"]:hover,
.form-control input[type="tel"]:hover,
