  margin: 0 auto;
  max-width: 140rem;
  padding: 0;
  position: relative;
  width: 90%;
}
.modal__body {
  display: flex;
  padding: 8rem 12rem;
}
.modal__left {
  flex-shrink: 0;
  margin-right: 8rem;
}
.modal__title {
  font-size: 4rem;
  font-weight: 700;
  margin-bottom: 0.5em;
}
.modal .socials {
  justify-content: center;
}
.modal .member__img {
  max-width: 40rem;
}
.modal .member__certificates {
