}
.modal .member__certificate {
  margin-bottom: 3.2rem;
  margin-top: -2.4rem;
}
.modal .member__certificate img {
  max-width: 100%;
  width: 10rem;
}
.modal .member__certificate:not(:last-child) {
  margin-right: 0.8rem;
}
.modal .member__xp {
  font-size: 1.8rem;
  font-weight: 700;
  text-align: center;
}
.fancybox-can-swipe .fancybox-content,
