.header {
  align-items: center;
  display: flex;
  margin-bottom: 2.4rem;
  min-height: 4.8rem;
}
.header__container {
  align-items: center;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
  padding: 3.2rem 0;
  position: relative;
}
.header__hover {
  bottom: 0;
  content: '';
  background: linear-gradient(90deg, rgba(0,109,213,0), #006dd5, rgba(0,109,213,0));
  height: 2px;
  left: 0;
  position: absolute;
  transform: translateY(4px);
  width: 10%;
}
.header__hover_active {
  transform: none;
}
.header__left {
  align-items: center;
  display: flex;
}
.header:hover .header__container:after {
  background: linear-gradient(90deg, rgba(0,109,213,0), #006dd5);
  height: 2px;
}
.nav__link {
  font-size: 1.2rem;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
.nav__link:not(:last-child) {
  margin-right: 1em;
}
.search {
  display: flex;
}
.search__input {
  background-color: transparent;
  border: none;
  border-bottom: 1px solid transparent;
  transition: all 0.4s ease;
}
.search__button {
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.footer {
  padding-bottom: 2.4rem;
}
.footer__container {
  color: #8a8c9a;
}
.footer__left {
  display: flex;
  margin-bottom: 2.4rem;
}
.index-links {
  margin-left: -2.4rem;
  margin-right: -2.4rem;
}
.index-link {
  display: block;
  background-color: #111;
  border-radius: 16px;
  overflow: hidden;
  padding: 2.4rem;
  position: relative;
}
.index-link__name {
  font-size: 2rem;
}
.index-link__text {
  line-height: 1.4;
  margin-bottom: 0.4em;
  opacity: 0.7;
}
.index-link__hover {
  opacity: 0;
  position: absolute;
  right: 2.2rem;
  top: 2.2rem;
  transform: translate(-0.8rem, 0.8rem);
  transition: all 0.4s ease;
}
.index-link:before {
  background-color: #006dd5;
  border-radius: 50%;
  content: '';
  filter: blur(40px);
  opacity: 0;
  position: absolute;
  right: -14rem;
  top: -14rem;
  transition: all 0.4s ease;
  height: 14rem;
  width: 14rem;
}
.index-link:hover:before {
  opacity: 1;
  right: -7rem;
  top: -7rem;
}
.index-link:hover .index-link__hover {
  opacity: 1;
  transform: none;
}
.page {
  background-color: $light-bg;
  min-height: 100vh;
}
.section {
  margin-bottom: 4.8rem;
}
.logo {
  display: inline-block;
  margin-right: 2.4rem;
  transform: translateY(-1px);
}
@media (min-width: 1200px) {
  .section {
    margin-bottom: 6.4rem;
  }
}
.lottie lottie-player {
  width: 100%;
  height: 10%;
}
.small-offset {
  margin-bottom: 1em;
}
.debug {
  position: relative;
}
.debug:after {
  background-color: rgba(255,0,0,0.2);
  outline: 1px solid #f00;
  content: '';
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 9999;
}
.text-gray {
  color: #8a8a8a;
}
